
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/todate/static/media/background.afb0ac25.jpg);
  /*background-size: 99vw 100vh ;*/
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header{
  font-family: 'Lobster', cursive;
  color: #00cc00;  
}
header h1 {
  margin: 35px 0px;
}
.line{
	border-color: #  #FFD700;
	border-width: 2px;
	border-style: groove;
}

p{
  font-family: 'Lobster', "Ubuntu", sans-serif;
  font-size: 250%;
  color: #DAA520;
  text-shadow: 1px 1px #FAFAD2;
}
.floatRight{
  float: right;
}
footer{
  position:fixed;bottom:30px;left:0;right:10px;height:50px;
  
}
footer p{
  color: #000000;
  text-shadow: 2px 2px #ff0000;  
  font-size: 100%;
}
.mainBody{
  padding-top: 30px;
}
.holidayList{
  padding-top: 30px;
  line-height: 40px;
  border-radius: 5px;
  border-width: 2px;
  border-color: #800000;
  border-style: groove;
}

.calendar{
  height: 100%;
  font-size: 150%;
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

